'use client';
import React, { useState } from 'react';
import Link from 'next/link';
import { HiOutlineMenuAlt2 } from 'react-icons/hi';

export default function NewSidebar() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  return (
    <>
      <div
        className={`fixed inset-0 bg-black bg-opacity-50 z-40 ${
          isOpen ? 'block' : 'hidden'
        }`}
        onClick={toggleSidebar}
      ></div>
      <div
        className={`fixed top-0 left-0 h-full w-64 bg-[#0000cd] text-[#ffffff] z-50 transform ${
          isOpen ? 'translate-x-0' : '-translate-x-full'
        } transition-transform duration-300 ease-in-out`}
      >
        <div className="p-4 mt-16">
          <h1 className="text-2xl font-bold">Luna</h1>
          <p className="text-sm mt-2">Şirket Adı</p>
          <nav className="mt-6">
            <ul>
              <li className="mb-4">
                <Link href="/homepage" className="hover:text-gray-400">
                  /homepage
                </Link>
              </li>
              <li className="mb-4">
                <Link href="/projects" className="hover:text-gray-400">
                  /projects
                </Link>
              </li>
              <li className="mb-4">
                <Link href="/project-tracking" className="hover:text-gray-400">
                  /project-tracking
                </Link>
              </li>
            </ul>
          </nav>
        </div>
      </div>
      <button
        onClick={toggleSidebar}
        className="fixed top-4 left-4 z-50 p-2 bg-[#001A78] text-[#eaf1f7] rounded-lg"
      >
        {isOpen ? '✕' : <HiOutlineMenuAlt2 size={32} />}
      </button>
    </>
  );
}
